import StatusFlowService from '@/services/commons/StatusFlowService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let StatusFlowTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultStatusFlowTableFilter: {
      type: String,
      default: null
    },
    defaultStatusFlowTableOrderBy: {
      type: String,
      default: null
    },
    statusTypeId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      statusFlowTableData: [],
      statusFlowTableFilterTimer: null,
      statusFlowTableFilter: this.defaultStatusFlowTableFilter,
      statusFlowTableOrderBy: this.defaultStatusFlowTableOrderBy,
      statusFlowTableContainer: 'loading-container',
      statusFlowTableBusy: false,
      statusFlowTableTotalRows: 0,
      statusFlowTableCurrentPage: 1,
      statusFlowTablePerPage: 10
    }
  },
  computed: {
    statusFlowTableNoDataText: function () {
      return this.statusFlowTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    statusFlowTableTotalPages: function () {
      return Math.ceil(this.statusFlowTableTotalRows / this.statusFlowTablePerPage)
    },
    statusFlowTablePagingInfo: function () {
      return this.pagingInfo(this.statusFlowTableCurrentPage, this.statusFlowTablePerPage, this.statusFlowTableTotalRows)
    }
  },
  methods: {
    loadStatusFlowTableData (loadingContainerId) {
      let self = this
      this.statusFlowTableBusy = true
      this.statusFlowTableContainer = loadingContainerId || this.statusFlowTableContainer
      this.startLoading(this.statusFlowTableContainer)
      StatusFlowService.statusFlows({
        pageIndex: this.statusFlowTableCurrentPage - 1,
        pageSize: this.statusFlowTablePerPage,
        filter: this.statusFlowTableFilter,
        orderByField: this.statusFlowTableOrderBy,
        statusTypeId: this.statusTypeId
      }).then(function (response) {
        self.stopLoading(self.statusFlowTableContainer)
        self.statusFlowTableBusy = false
        self.statusFlowTableTotalRows = response.data.totalRows
        self.statusFlowTableData = response.data.statusFlowList || []
      }).catch(function (error) {
        self.stopLoading(self.statusFlowTableContainer)
        self.statusFlowTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshStatusFlowTable () {
      this.loadStatusFlowTableData()
    },
    filterStatusFlowTable (filter) {
      let self = this
      if (this.statusFlowTableFilter !== filter) {
        clearTimeout(this.statusFlowTableFilterTimer)
        this.statusFlowTableFilterTimer = setTimeout(() => {
          self.statusFlowTableFilter = filter
          self.loadStatusFlowTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.statusFlowTableFilterTimer)
      }
    },
    orderStatusFlowTable (orderBy, active) {
      this.statusFlowTableOrderBy = (active ? '' : '-') + orderBy
      this.loadStatusFlowTableData()
    }
  },
  watch: {
    statusFlowTableCurrentPage: function () {
      this.refreshStatusFlowTable()
    },
    statusFlowTablePerPage: function () {
      this.refreshStatusFlowTable()
    },
    statusTypeId: function () {
      this.refreshStatusFlowTable()
    }
  }
}

export default StatusFlowTableMixin
