<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
      color="rgba(var(--vs-primary), 1)"
      :title="null" :subtitle="null"
      :nextButtonText="$t('NEXT')"
      :backButtonText="$t('PREVIOUS')">

      <template slot="custom-buttons-left" slot-scope="props">
        <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
      </template>
      <template slot="finish">
        <div class="dropdown-button-container">
          <vs-button class="btnx" type="filled" @click="createWorkflow('browse')">{{ $t('SUBMIT') }}</vs-button>
          <vs-dropdown vs-trigger-click>
            <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
            <vs-dropdown-menu>
              <vs-dropdown-item @click="createWorkflow('stay')">
                <span>{{ $t('SUBMIT_STAY') }}</span>
              </vs-dropdown-item>
              <vs-dropdown-item @click="createWorkflow('view')">
                <span>{{ $t('SUBMIT_VIEW') }}</span>
              </vs-dropdown-item>
            </vs-dropdown-menu>
          </vs-dropdown>
        </div>
      </template>

      <tab-content :title="$t('GENERAL')" icon="far fa-file-alt">
        <div class="nv-row mb-4">
          <div class="nv-col sm:w w-full required">
            <label class="vs-input--label">{{ $t('WORKFLOW_NAME') }}</label>
            <vs-input class="w-full" v-model="workflowName"/>
          </div>
        </div>
        <div class="nv-row mb-4">
          <div class="nv-col sm:w-1/2 w-full required">
            <label class="vs-input--label">{{ $t('WORKFLOW_TYPE') }}</label>
            <vs-select autocomplete class="w-full" v-model="workflowTypeId">
              <vs-select-item v-for="(workflowType, index) in workflowTypes" :key="index" :value="workflowType.typeId" :text="workflowType.typeName"/>
            </vs-select>
          </div>
          <div class="nv-col sm:w-1/2 w-full required">
            <label class="vs-input--label">{{ $t('STATUS_FLOW') }}</label>
            <status-flow-lookup :title="$t('STATUS_FLOW')" :recordValue="statusFlowId" :recordDisplay="statusFlowDescription" valueField="statusFlowId" displayField="description" :statusTypeId="statusTypeId" @select-record="selectStatusFlowRecord"/>
          </div>
        </div>
        <div class="nv-row mb-4">
          <div class="nv-col w-full">
            <label class="vs-input--label">{{ $t('DESCRIPTION') }}</label>
            <vs-textarea class="w-full" v-model="description"/>
          </div>
        </div>
        <div class="nv-row mb-4">
          <div class="nv-col w-full">
            <vs-checkbox v-model="disabled">{{ $t('DISABLED') }}</vs-checkbox>
          </div>
        </div>
      </tab-content>

      <tab-content :title="$t('REMINDERS')" icon="far fa-alarm-clock">
        <div class="nv-row mb-4">
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('REMINDER_INTERVAL') }}</label>
            <div class="flex">
              <div class="flex mr-4">
                <vs-select autocomplete v-model="reminderIntervalUomId">
                  <vs-select-item v-for="(interval, index) in timeIntervals" :key="index" :value="interval.uomId" :text="interval.description"/>
                </vs-select>
              </div>
              <div class="flex">
                <vs-input-number v-model="reminderInterval" min="0"/>
              </div>
            </div>
          </div>
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('REMINDER_LIMIT') }}</label>
            <div class="flex">
              <vs-input-number v-model="reminderLimit" min="0"/>
            </div>
          </div>
        </div>
      </tab-content>

      <tab-content :title="$t('INITIATORS')" icon="far fa-users">
        <div class="inline-form p-4 mb-base" v-if="userGroupFormVisible">
          <div class="nv-row mb-4">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('USER_GROUP') }}</label>
              <user-group-lookup :title="$t('USER_GROUP')" valueField="userGroupId" displayField="description" @select-record="selectUserGroupRecord"/>
            </div>
          </div>
          <div class="nv-row">
            <div class="nv-col sm:w-1/2 w-full">
              <div class="flex justify-left">
                <vs-button class="px-5 py-3 mr-2" @click="addUserGroup" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
                <vs-button class="px-5 py-3 mr-2" @click="hideUserGroupForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
              </div>
            </div>
          </div>
        </div>

        <div class="flex mb-base">
          <vs-button class="p-3 mr-4" @click="showUserGroupForm" :disabled="userGroupFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far">{{ $t('ADD') }}</vs-button>
        </div>

        <vs-alert :active.sync="userGroups.length === 0" color="warning" class="mb-base">
          <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
        </vs-alert>

        <vs-table class="table-no-header mb-4" :data="userGroupTableData" v-if="userGroups.length > 0">
          <template slot="thead">
            <vs-th sort-key="userGroupId">
              {{ this.$t('ID') }}
            </vs-th>
            <vs-th sort-key="description">
              {{ this.$t('DESCRIPTION') }}
            </vs-th>
            <vs-th>
              {{ this.$t('OPTIONS') }}
            </vs-th>
          </template>

          <template slot-scope="{data}">
            <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
              <vs-td :data="data[index].userGroupId">
                {{ data[index].userGroupId }}
              </vs-td>
              <vs-td :data="data[index].description">
                {{ data[index].description }}
              </vs-td>
              <vs-td :data="data[index].options">
                <vs-button class="p-3 mr-4" color="danger" @click="deleteUserGroup(index)" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </tab-content>
    </form-wizard>
  </nv-card>
</div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import WorkflowService from '@/services/commons/WorkflowService'
import UomService from '@/services/commons/UomService'
import StatusFlowService from '@/services/commons/StatusFlowService'
import StatusFlowLookup from '@/views/commons/status-flow/StatusFlowLookup'
import UserGroupLookup from '@/views/nucleus/user-group/UserGroupLookup'
import { FormWizard, TabContent } from 'vue-form-wizard'

export default {
  name: 'workflow-create',
  mixins: [AlertMixin, LoadingMixin],
  components: {
    FormWizard,
    TabContent,
    StatusFlowLookup,
    UserGroupLookup
  },
  data: function () {
    return {
      workflowName: '',
      timeIntervals: [],
      workflowTypes: [],
      workflowTypeId: '',
      statusFlowId: '',
      statusFlowDescription: '',
      reminderInterval: 0,
      reminderIntervalUomId: 'TF_hr',
      reminderLimit: 0,
      description: '',
      disabled: false,
      userGroupTableData: [],
      userGroups: [],
      userGroupFormVisible: false,
      userGroupId: '',
      userGroupDescription: ''
    }
  },
  computed: {
    statusTypeId () {
      for (let i = 0; i < this.workflowTypes.length; i++) {
        if (this.workflowTypes[i].typeId === this.workflowTypeId) {
          this.loadStatusFlow(this.workflowTypes[i].statusTypeId)
          return this.workflowTypes[i].statusTypeId
        }
      }
      return null
    }
  },
  methods: {
    async load () {
      let self = this
      WorkflowService.workflowTypes({
        orderByField: 'typeName'
      }).then(function (response) {
        self.workflowTypes = response.data.workflowTypeList || []
        self.workflowTypeId = self.workflowTypes.length > 0 ? self.workflowTypes[0].typeId : ''
      })

      UomService.uoms({
        uomTypeEnumId: 'UT_TIME_FREQ_MEASURE',
        orderByField: 'description',
        pageNoLimit: true
      }).then(function (response) {
        self.timeIntervals = response.data.uomList || []
      })
    },
    loadStatusFlow (statusTypeId) {
      let self = this
      StatusFlowService.statusFlows({
        statusTypeId: statusTypeId
      }).then(function (response) {
        let statusFlows = response.data.statusFlowList || []
        if (statusFlows.length === 1) {
          self.selectStatusFlowRecord(statusFlows[0])
        }
      })
    },
    selectStatusFlowRecord (record) {
      this.statusFlowId = record.statusFlowId || ''
      this.statusFlowDescription = record.description || ''
    },
    showUserGroupForm () {
      this.userGroupFormVisible = true
    },
    hideUserGroupForm () {
      this.userGroupFormVisible = false
      this.userGroupId = ''
      this.userGroupDescription = ''
    },
    selectUserGroupRecord (record) {
      this.userGroupId = record.userGroupId || ''
      this.userGroupDescription = record.description || ''
    },
    addUserGroup () {
      if (this.userGroupId !== '' && this.userGroupDescription !== '') {
        this.userGroupTableData.push({
          userGroupId: this.userGroupId,
          description: this.userGroupDescription
        })
        this.userGroups.push(this.userGroupId)
        this.hideUserGroupForm()
      }
    },
    deleteUserGroup (index) {
      this.userGroupTableData.splice(index, 1)
      this.userGroups.splice(index, 1)
    },
    createWorkflow (nextAction) {
      let self = this
      this.startLoading('loading-container')
      WorkflowService.createWorkflow({
        workflowTypeId: this.workflowTypeId,
        workflowName: this.workflowName,
        description: this.description,
        statusFlowId: this.statusFlowId,
        disabled: this.disabled ? 'Y' : 'N',
        reminderInterval: this.reminderInterval,
        reminderIntervalUomId: this.reminderIntervalUomId,
        reminderLimit: this.reminderLimit,
        userGroupId: this.userGroups.join(',')
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewWorkflow', params: { id: response.data.workflowId } })
          } else {
            self.$router.push({ name: 'BrowseWorkflows' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
